.page-title {
  margin-top: 8%;
  text-align: center;
}

.search-input {
  margin-bottom: 5%;
}

.table-container {
  width: 80%;
  margin: auto;
  margin-top: 5%;
}

.addButton {
  cursor: pointer;
  font-size: x-large;
  margin: auto;
  margin-left: 1%;
  margin-right: 1%;
}

.inputWrapper {
  display: flex;
  margin-bottom: 2%;
}

.actionButton {
  cursor: pointer;
  font-size: large;
  margin: auto;
  margin-left: 3%;
  margin-right: 3%;
}

.logo {
  width: 90%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo img {
  max-width: 100%;
  max-height: 100%;
}

@media (max-width: 600px) {
  .column-description {
    display: none;
  }
}
